<template>
	<div class='partner-wrapper'>
		<ul class="partner-slider" @mouseover = 'showdetail' @mouseout='hidedetail'>
			<li v-for='item in partner' ref='partnerimg'>
				<img :src="item.url">
					<div class="partner-name">
						<p>{{item.name}}</p>
				<transition name='fade'>
						<p v-if='show'>{{item.detail}}</p>
				</transition>
					</div>					

			</li>
		</ul>
	</div>
</template>

<script type="text/javascript">
	export default{
		props:[
		'partner'
		],
		mounted(){
			setTimeout(() => {
			  this.initslider()
			}, 20)
		},
		methods:{
			initslider(){
				setInterval(() => {
				  this.currentIndex += 1
				  if(this.currentIndex >= 6){
				  	this.currentIndex =0
				  }
					var imgitems = this.$refs.partnerimg
					imgitems.forEach((item) => {
					  item.style.display = 'none'
					})
					imgitems[this.currentIndex].style.display = 'block'
				}, 2000)
			},
			showdetail(){
				this.show = true
			},
			hidedetail(){
				this.show = false
			}
		},
		data(){
			return {
				currentIndex:1,
				show : false,

			}
		}
	}
</script>

<style type="text/css" lang='less'>
	.partner-wrapper{
		overflow: hidden;
		margin: 0 auto;
		width: 800px;
		height: 400px;
	}
	.partner-slider{
		width: 600%;
		height: 400px;
		background-color: pink;
		margin:0 auto;
		position: relative;
		li{
			float: left;
			position: absolute;
			img{
				width: 800px;
				height: 400px;
			}
			.partner-name{
				position: absolute;
				bottom:0;
				left: 0;
				width:100%;
				background-color: rgba(0,0,0,0.5);
				color: white;
				p:nth-child(1){
					font-size:16px;
					font-weight: bold;
					height: 40px;
					line-height: 40px;
				}
				p:nth-child(2){
					font-size: 14px;
					height: 30px;
					line-height: 30px;
				}
			}
		}
	}
	.fade-enter,.fade-leave-to{
		opacity: 0;
		transform:translateY(10px)
	}

	.fade-enter-active , .fade-leave-active{
		transition: 1s all ease;
	}
</style>


