<template>
	<div class="tem-product">
		<div class="kind-title">
			<span>{{products.kind}}</span>
			<span>more</span>
		</div>
		<ul class="clearfix">
			<li v-for='(item,index) in products.product' @mouseover='scale(index)' @mouseout='origin(index)' ref='liitem'>
				<div class="hot" v-if="item.hot">hot</div>
				<a href="#">
					<div class="product-img">
						<img src="../assets/sf-product.png">
					</div>
					<div class="pro-type">
						<span>{{item.name}}</span><span>{{item.type}}</span>
					</div>
					<div class="pro-name">{{item.detail}}</div>
				</a>
			</li>
		</ul>
	</div>
</template>

<script type="text/javascript">
	export default{
		props:{
			products:{
				type:Object
			}
		},
		methods:{
			scale(index){
				this.$refs.liitem[index].style.transform = 'scale(1.1,1.1)'
				this.$refs.liitem[index].style.transition = "all 0.5s"
			},
			origin(index){
				this.$refs.liitem[index].style.transform = 'scale(1,1)'
			}
		}
	}
</script>

<style type="text/css" lang='less'>

	.tem-product{
		width: 1000px;
		margin: 0 auto;
		.kind-title{
			background-color: pink;
			height: 54px;
			span:nth-child(1){
				font-size: 22px;
				margin-left: 24px;
				line-height: 54px;
				float: left;
			}
			span:nth-child(2){
				font-size: 16px;
				margin-right: 24px;
				line-height: 54px;
				float: right;
				cursor: pointer;
			}
		}	
		ul{
			margin:30px auto 0;
			li{
				position: relative;
				.hot{
					height: 20px;
					width: 40px;
					background-color: red;
					color: white;
					position: absolute;
				}
				float: left;
				width: 25%;
				height: 350px;
				background-color: #ccc;
				img{
					margin:30px auto;
				}
				.pro-type{
					font-size: 18px;
					span:nth-child(1){
						color: black;
					}
					span:nth-child(2){
						color: red;
					}
				}
				.pro-name{
					font-size: 16px;
					color: #8f8f8f
				}
			}
		}	
	}
</style>


